.nav__btn {
  width: $widthNavBtn;
  height: $heightNavBtn;
  margin-left: 35px;
  border: 3px solid #000;
  border-radius: $btnBorderRadius;
  background-color: transparent;
  color: black;
  cursor: pointer;
  transition: background-color, color 0.5s;

  &:hover {
    background-color: #000;
    color: white;

  }
}

.btn {
  @include btn;

  &:hover {
    color: white;
    background-color: #ff699f;
  }
}
